<template>
  <div class="container">
    <div class="box">
      <el-row class="header">
        <el-col class="icon" :span="3">
          <transition name="icon" >
            <van-icon 
            v-show="!show " 
            size="1.4em" 
            name="wap-nav" 
            class="iconfont icon-checkmark"
            @click="getshow()"
            />
          </transition>
          <transition name="icon">
          <van-icon 
            v-show="show && !isMobile"
            size="1.4em" 
            name="arrow-up" 
            class="iconfont icon-close"
            @click="getshowclose()"
            />
          </transition>
        </el-col>
        <el-col :span="18">
          <h1 class="logo">Lin Blog</h1>
        </el-col>
        <el-col :span="3">
        </el-col>
      </el-row>

      <el-drawer
        v-if="isMobile"
        :visible.sync="show"
        direction="ltr"
        size="300px"
        append-to-body
        :modal="false"
        >
        <div>
          <span>我来啦!</span>
        </div>
      </el-drawer>

      <el-drawer
        v-else
        :visible.sync="show"
        direction="ttb"
        size="200px"
        append-to-body
        :modal="false"
        :show-close="false"
        @open="handleDrawerOpen"
        :style="{ top: drawerTop + 'px' }"
        >
        <div>
          <span>我来啦!</span>
        </div>
      </el-drawer>

    </div>
    <div class="box-1"></div>
    <el-row class="content">
      <el-col :span="24" :md="16">
        <div class="main">
          <van-swipe class="my-swipe" :autoplay="5000" indicator-color="white" >
            <van-swipe-item v-for="(item,index) in photo" :key="index">
              <img  :src="item.content" alt="">
            </van-swipe-item>
          </van-swipe>
        </div>
      </el-col>
    </el-row>

    

    <el-row class="footer">
      <el-col :span="24">
        <div class="bottom">
          <p>&copy; 2023 Lin Blog</p>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: '1',
      show: false,
      isMobile:true,
      photo:[],
      drawerTop: 0,
    };
  },
  methods: {
    getData(){
      this.photo = window.photo
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    getshow(){
      this.show = true
    },
    getshowclose(){
      this.show = false
    },
    handleDrawerOpen() {
      const headerHeight = document.querySelector('.box').offsetHeight
      this.drawerTop = headerHeight
    },

  },
  updated() {
    this.isMobile = /mobile/i.test(navigator.userAgent)
  },
  mounted() {
    this.isMobile = /mobile/i.test(navigator.userAgent)
    this.getData()
  }
};
</script>

